html {
  height: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  counter-reset: section;
  margin: 0 auto;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  font-family: $basic-font-family;
  flex-direction: column;
  word-wrap: break-word;
  font-size: 21px;
  line-height: 24px;
  color: $black;
  background-color: $gray-concrete;
  text-transform: uppercase;
  text-align: center;

  @media (min-width: $tablet-width) {
    font-size: 13px;
  }

  @media (min-width: $desktop-width) {
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
  }
}

.page-wrapper {
  @include container;
}

img {
  max-width: 100%;
  height: auto;
}

main {
  display: block;
  background-color: #ffffff;

  @media (min-width: $desktop-width) {
    max-width: 1200px;
    margin: 0 auto;
  }
}
