
.features__title {
  @include font-set(18px, 26px, 700);
  position: relative;
  color: #000000;
  margin: 0;
  margin-bottom: 14px;
  margin-top: 0;
  text-align: center;
  text-transform: uppercase;

  &::after {
    display: block;
    counter-increment: section;
    content: " " counter(section) " ";
    font-size: 14px;
    font-weight: 400;
    padding: 9px 67px 0 67px;
  }
}

.features__title-list {
  margin: 0;
  padding: 0;
  position: relative;
  margin-bottom: 13px;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  text-align: left;
}

@media (min-width: $tablet-width) {
  .features__title {
    font-size: 21px;
    font-weight: 700;
    background-position: top center;
    background-repeat: no-repeat;
    text-align: center;

    &::after {
      padding-top: 8px;
      padding-bottom: 4px;
      padding-left: 0;
      padding-right: 0;
    }
  }

  .features__title-list {
    text-align: center;
    font-size: 22px;
    margin-bottom: 22px;
  }
}

@media (min-width: $desktop-width) {
  .features__title {
    margin: 4px 57px 7px;

    &::after {
      padding-top: 8px;
      padding-bottom: 11px;
    }
  }

  .features__title-list {
    margin-bottom: 21px;
    margin-top: -2px;
  }
}
